<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
        <img src="../assets/css/login.jpg" />
      </div>
      <!--  -->
      <!--  -->
      <el-form label-width="0" class="login_form" :model="LoginForm" :rules="loginRules" ref="loginFormRef">
        <el-form-item prop="username">
          <el-input prefix-icon="iconfont icon-user" v-model="LoginForm.username"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input prefix-icon="iconfont icon-3702mima" v-model="LoginForm.password" type="password"></el-input>
        </el-form-item>
        <!--    -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      // 
      LoginForm: {
        username: '',
        password: ''
      },
      // 
      loginRules: {
        // 
        username: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
          ],
        // 
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 
    resetLoginForm() {
      this.$refs.loginFormRef.resetFields()
    },
    login() {
      this.$refs.loginFormRef.validate(async validate => {
        if(!validate) return
        const {data : res} = await this.$http.post('login',this.LoginForm)
        if(res.meta.status !== 200) return  this.$message.error('')
        this.$message.success('')
        // 1.token,sessionStorage
        //   1.1API,
        //   1.2 token, tokensessionstorge
        window.sessionStorage.setItem("token",res.data.token)
        // 2.,/home
        this.$router.push('/home')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login_container {
  background-color: #2b4b6b;
  height: 100%;
}

.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  .avatar_box {
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    box-shadow: 0 0 20px grey;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: flex-end;
}
</style>
