<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <!--  -->
    <el-card>
      <el-alert show-icon title="" type="warning" :closable="false"></el-alert>
      <!--  -->
      <el-row class="cat_opt">
        <el-col>
          <span></span>
          <!--  -->
          <el-cascader v-model="selectedCateKeys" :options="cateList" :props="cascaderParams" @change="handleChange"></el-cascader>
        </el-col>
      </el-row>
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <el-tab-pane label="" name="many">
          <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true"></el-button>
          <!--  -->
          <el-table :data="manyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i,scope.row)" :disable-transitions="false">{{ item }}</el-tag>
                <!--  -->
                <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)"> </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.attr_id)"></el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeParams(scope.row.attr_id)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true"></el-button>
          <!--  -->
          <el-table :data="onlyTabData" border stripe>
            <!--  -->
            <el-table-column type="expand">
               <template slot-scope="scope">
                <el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i,scope.row)" :disable-transitions="false">{{ item }}</el-tag>
                <!--  -->
                <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)"> </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.attr_id)"></el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeParams(scope.row.attr_id)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--  -->
    <el-dialog :title="'' + titleText" :visible.sync="addDialogVisible" width="50%" @close="addDialogClose">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px" class="demo-ruleForm">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParams"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog :title="'' + titleText" :visible.sync="EditDialogVisible" width="50%" @close="EditDialogClose">
      <el-form :model="EditForm" :rules="EditFormRules" ref="EditFormRef" label-width="100px" class="demo-ruleForm">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="EditForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="EditDialogVisible = false"> </el-button>
        <el-button type="primary" @click="EditParams"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      cascaderParams: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      //
      cateList: [],
      selectedCateKeys: '',
      // 
      activeName: 'many',
      manyTableData: [],
      onlyTabData: [],
      addDialogVisible: false,
      EditDialogVisible: false,
      addForm: {
        attr_name: ''
      },
      EditForm: {},
      addFormRules: {
        attr_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      EditFormRules: {
        attr_name: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.cateList = res.data
    },
    handleChange() {
      this.getParamsData()
    },
    handleTabClick() {
      this.getParamsData()
    },
    // 
    async getParamsData() {
      // 
      if (this.selectedCateKeys.length !== 3) {
        this.selectedCateKeys = []
        this.manyTableData = []
        this.onlyTabData = []
        return
      }
      // Id,
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
        params: {
          sel: this.activeName
        }
      })
      if (res.meta.status !== 200) return this.$message.error('')
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
        item.inputVisible = false
        item.inputValue = ''
      })
      if (this.activeName === 'many') {
        return (this.manyTableData = res.data)
      } else {
        return (this.onlyTabData = res.data)
      }
    },
    addDialogClose() {
      this.$refs.addFormRef.resetFields()
    },
    addParams() {
      // 
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post(`categories/${this.cateId}/attributes`, {
          attr_name: this.addForm.attr_name,
          attr_sel: this.activeName
        })
        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        ;(this.addDialogVisible = false), this.getParamsData()
      })
    },
    // 
    async showEditDialog(attr_id) {
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes/${attr_id}`, {
        params: {
          attr_sel: this.activeName
        }
      })
      if (res.meta.status !== 200) return this.$message.error('')
      this.EditForm = res.data
      this.EditDialogVisible = true
    },
    EditDialogClose() {
      this.$refs.EditFormRef.resetFields()
    },
    EditParams() {
      this.$refs.EditFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${this.EditForm.attr_id}`, {
          attr_name: this.EditForm.attr_name,
          attr_sel: this.activeName
        })
        res.meta.status !== 200 ? this.$message.error('') : this.$message.success('')
        this.getParamsData()
        this.EditDialogVisible = false
      })
    },
    async removeParams(attr_id) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete(`categories/${this.cateId}/attributes/${attr_id}`)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getParamsData()
    },
    handleInputConfirm(row) {
      if(row.inputValue.trim().length === 0) 
      {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false
      // 
     this.saveAttrVals(row)
     
    },
    // 
    async saveAttrVals(row) {
       const {data: res} = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,{
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        attr_vals: row.attr_vals.join(',')
      })
      res.meta.status !== 200 ? this.$message.error('') : this.$message.success('')
    },
    showInput(row) {
      row.inputVisible = true
      // 
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    handleClose(i,row) {
      row.attr_vals.splice(i,1)
      this.saveAttrVals(row)
    }
  },
  computed: {
    // true,true
    isBtnDisabled() {
      if (this.selectedCateKeys.length !== 3) return true
      return false
    },
    cateId() {
      if (this.selectedCateKeys.length === 3) return this.selectedCateKeys[2]
      return null
    },
    // 
    titleText() {
      if (this.activeName === 'many') return ''
      return ''
    }
  }
}
</script>

<style lang="less" scoped>
.cat_opt {
  margin: 15px 0;
}
/deep/ .el-table td.el-table__cell div {
 text-align: center;
}
/deep/ .el-table th.el-table__cell > .cell {
  text-align: center;
}
.el-tag {
  margin: 8px 10px;
}
.input-new-tag {
  width: 120px;
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
}
</style>
