<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getTableData">
            <el-button slot="append" icon="el-icon-search" @click="getTableData"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddPage"></el-button>
        </el-col>
      </el-row>
      <el-table :data="tableData" border stripe max-height="700px">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="goods_name" label=""> </el-table-column>
        <el-table-column prop="goods_price" label="" width="95px"></el-table-column>
        <el-table-column prop="goods_weight" label="" width="70px"></el-table-column>
        <el-table-column prop="add_time" label="" width="140px">
          <template slot-scope="scope">
            {{ scope.row.add_time | dateFoemat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="130px">
          <template slot-scope="scope">
            <!--  -->
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <!--  -->
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeById(scope.row.goods_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[5, 10, 15, 20]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total" background> </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      total: 0
    }
  },
  created() {
    this.getTableData()
  },
  methods: {
    async getTableData() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) return this.$message.error('')
      this.tableData = res.data.goods
      this.total = res.data.total
    },
    handleSizeChange(newPagesize) {
      this.queryInfo.pagesize = newPagesize
      this.getTableData()
    },
    handleCurrentChange(pagenum) {
      this.queryInfo.pagenum = pagenum
      this.getTableData()
    },
    async removeById(id) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete(`goods/${id}`)
      res.meta.status !== 200 ? this.$message.error('') : this.$message.success('')
      this.getTableData()
    },
    goAddPage() {
      this.$router.push('/goods/add')
    }
  }
}
</script>

<style lang="less" scoped></style>
