<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"></el-button>
        </el-col>
      </el-row>
      <!-- () -->
      <tree-tabel :data="cateList" :columns="columns" :selection-type="false" :expand-type="false" show-index index-text="#" border :show-row-hover="false" class="treeTable">
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color: lightgreen"></i>
          <i class="el-icon-error" style="color: lightgreen"></i>
        </template>
        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cate_level === 0"></el-tag>
          <el-tag size="mini" type="success" v-else-if="scope.row.cate_level === 1"></el-tag>
          <el-tag size="mini" type="warning" v-else></el-tag>
        </template>
        <!--  -->
        <template slot="opt">
          <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini"></el-button>
        </template>
      </tree-tabel>
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[3, 5, 10, 15]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total"> </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addCateDialogVisible" width="50%" @close="addCateDialogClosed">
      <el-form :model="addCateForm" :rules="addCateFormrules" ref="addCateFormRef" label-width="100px">
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <!-- options -->
          <el-cascader v-model="selectedKeys" :options="parentCateList" :props="cascaderProps" @change="parentCateChange" clearable ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      cateList: [],
      // 
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 10
      },
      // ()
      total: 0,
      // table
      columns: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          // 
          type: 'template',
          template: 'isok'
        },
        {
          label: '',
          // 
          type: 'template',
          template: 'order'
        },
        {
          label: '',
          type: 'template',
          template: 'opt'
        }
      ],
      // 
      addCateDialogVisible: false,
      addCateForm: {
        cat_level: 0,
        cat_name: '',
        cat_pid: 0
      },
      addCateFormrules: {
        cat_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      parentCateList: [],
      cascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id  ',
        label: 'cat_name',
        children: 'children',
        checkStrictly: true
      },
      selectedKeys: []
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      if (res.meta.status != 200) return this.$message.error('')
      this.cateList = res.data.result
      // 
      this.total = res.data.total
    },
    // pagesize
    handleSizeChange(newsize) {
      this.queryInfo.pagesize = newsize
      this.getCateList()
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCateList()
    },
    // 
    showAddCateDialog() {
      // 
      this.getParentCateList()
      this.addCateDialogVisible = true
    },
    // 
    async getParentCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 3 }
      })
      if (res.meta.status != 200) return this.$message.error('')
      this.parentCateList = res.data
    },
    parentCateChange() {
      // selectedkeyslength>0,
      // 
      if (this.selectedKeys.length > 0) {
        // Id
        this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
        // 
        this.addCateForm.cat_level = this.selectedKeys.length
        return
      } else {
        this.addCateForm.cat_pid = 0,
        this.addCateForm.cat_level = 0
      }
    },
    // 
    addCate() {
        this.$refs.addCateFormRef.validate(async validate => {
        if( !validate) return
        const {data: res} = await this.$http.post('categories',this.addCateForm)
        if(res.meta.status !== 201) return this.$message('')
        this.$message.success('')
        this.getCateList()
        this.addCateDialogVisible = false
      })
    },
    // 
    addCateDialogClosed() {
      this.$refs.addCateFormRef.resetFields(), 
      this.selectedKeys = [],
      this.addCateForm.cat_level = 0,
      this.addCateForm.cat_pid = 0
    }
  }
}
</script>

<style lang="less" scoped>
.treeTable {
  margin-top: 15px;
}
</style>
