<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="dialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="rolelist" border stripe height="700px">
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row v-for="(item1, i1) in scope.row.children" :key="item1.id" :class="['bdbottom ', i1 === 0 ? 'bdtop' : '', 'vcentuer']" id="Rightexpand">
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row, item1.id)">
                  {{ item1.authName }}
                </el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!-- for -->
                <el-row v-for="item2 in item1.children" :key="item2.id" id="Rightexpand2" class="vcentuer">
                  <el-col :span="6">
                    <el-tag type="success" closable @close="removeRightById(scope.row, item2.id)">{{ item2.authName }}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag v-for="item3 in item2.children" :key="item3.id" type="warning" closable @close="removeRightById(scope.row, item3.id)">
                      {{ item3.authName }}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit"></el-button>
            <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeRole(scope.row.id)"></el-button>
            <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="50%" @close="addRoleClose">
      <el-form :model="addRoleForm" :rules="addFormrules" ref="addRoleRef" label-width="60px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="addRoleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRole"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="setRightdialogVisible" width="50%" @close="setDialogClose">
      <!--  -->
      <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightdialogVisible = false"> </el-button>
        <el-button type="primary"  @click="allotRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      rolelist: [],
      dialogVisible: false,
      setRightdialogVisible: false,
      addRoleForm: {
        roleName: '',
        roleDesc: ''
      },
      addFormrules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: '3~10', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: '3~10', trigger: 'blur' }
        ]
      },
      // 
      rightsList :[],
      // 
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      // ID
      defKeys: [],
      // Id
      roleid:''
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    async getRolesList() {
      const { data: res } = await this.$http.get('/roles')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rolelist = res.data
    },
    // 
    async addRole() {
      // 
      const { data: res } = await this.$http.post('/roles', this.addRoleForm)
      if (res.meta.status !== 201) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.dialogVisible = false
      // 
      this.getRolesList()
    },
    // Id
    async removeRole(id) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(error => error)
      //  confirm
      //  cancel
      // console.log(confirmResult)

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getRolesList()
    },
    // 
    addRoleClose() {
      this.$refs.addRoleRef.resetFields()
    },
    // 
    async showSetRightDialog(role) {
      this.roleid = role.id
      // 
      const {data: res} = await this.$http.get('/rights/tree')
      if(res.meta.status !== 200) return this.$message.error('')
      // data
      this.rightsList = res.data
      // id
      this.getLeafKeys(role,this.defKeys)
      this.setRightdialogVisible = true
    },
    // tag
    async removeRightById(role, rightid) {
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.error('!')
      }
      const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rightid}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      role.children = res.data
    },
    // id,defKeys
    getLeafKeys(node, arr) {
      // nodechildren,
      if(!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => {
        this.getLeafKeys(item, arr)
      })
    },
    // bug
    setDialogClose() {
      this.defKeys = []
    },
    // 
    async allotRights() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const {data: res} = await this.$http.post(`roles/${this.roleid}/rights`,{rids: idStr})
      if(res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getRolesList()
      this.setRightdialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.bdtop {
  border: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.el-tag {
  margin: 10px;
}
#Rightexpand {
  margin-left: 48px;
  margin-right: 48px;
  border-left: none;
  border-right: none;
}
#Rightexpand2 {
  border-top: 1px solid #eee;
}
#Rightexpand2:first-child {
  border-top: none;
}
.vcentuer {
  display: flex;
  align-items: center;
}
/deep/ .el-form-item__label {
  width: 48px;
  line-height: 19px;
  padding: 0 18px 0 0;
}
</style>
